.mainMenu {
  display: flex;
  align-items: center;
  width: 100%;
  color: $brand-yellow;
  background-color: $brand-lightBlue;
  @include media-md {
    height: 65px;
  }
}

.navWrapper {
  width: 100%;
}

.headerSmall {
  display: flex;
  justify-content: space-between;
  width: 100%;
  @include media-md {
    display: none;
  }
}

.headerBig {
  display: none;
  @include media-md {
    display: inline;
    width: 100%;
  }
}

.logo > a {
  font-family: "Yellowtail", cursive;
  margin: 15px 0;
  color: $brand-yellow;
  font-size: 2rem;
  font-weight: 400;
  display: block;
  transition: .3s ease transform;
  white-space: nowrap;
  @include media-md {
    margin: 0px;
    font-size: 2rem;
    &:hover {
      transform: scale(1.05);
    }
  }
  @include media-lg {
    font-size: 2.2rem;
  }
}

.navBar {
  width: 100%;
  a {
    color: $brand-yellow;
    text-decoration: none;
  }
  ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    list-style-type: none;
    padding: 0;
    margin: 0;
    li {
      display: inline;
      font-family: "Khula";
      font-weight: 700;
      font-size: .875rem;
      letter-spacing: 1.14px;
      line-height: 16px;
      margin-top: 0;
      margin-bottom: 0;
      // selecting the menu items without logo and phone
      &:nth-of-type(n+2) {
        &:not(#phoneIcon) {
          padding-top: .4rem;
          text-transform: uppercase;
          &:hover {
            padding-top: calc(.4rem + 2px);
            border-bottom: 2px solid $brand-orange;
          }
      }
      &.activePage {
        padding-top: calc(.4rem + 2px);
        border-bottom: 2px solid $brand-orange;
      }
    }
      @include media-xl {
        font-size: 1rem;
        line-height: 20px;
      }
    }
  }
}



#phoneIcon {
  a {
    i {
      height: 50px;
      width: 50px;
      border-radius: 50%;
      color: $brand-orange;
      background-color: $brand-darkBlue;
      font-size: 1.6rem;
      padding-top: 1rem;
      text-align: center;
      transition: .3s ease background-color, color;
      &:hover {
        background-color: $brand-yellow;
        color: $brand-darkBlue;
      }
    }
  }
}

.hamburger {
  display: flex;
  width: 40px;
  height: 40px;
  margin: 15px 0;
  border-radius: 5px;
  padding: 0;
  font-size: 0.75rem;
  color: $brand-lightBlue;
  background-color: $brand-yellow;
  align-items: center;
  justify-content: center;
  transition: .3s ease all;
  &:hover {
    border: 1px solid $brand-orange;
  }
}

.hamburger-box {
  span {
    width: 30px;
  }
}

