// Offset the main page content by the menu width.
.main {
  position: relative;
  top: 0;
  left: 0;
  &.open {
    height: 100vh;
    width: 100vw;
    overflow-y: scroll;
    left: -275px;
  }
}

// The main modal page with a class for toggling its display.
.menu-banner {
  display: none;
  justify-content: flex-end;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1001;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
  &.open {
    display: flex;
  }
}

// The menu wrapper.
.menu-wrapper {
  padding: 30px 0 0 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 275px;
  height: 100%;
  overflow-y: scroll;
  background-color: $brand-lightBlue;
  color: $brand-yellow;
  line-height: 1.4;
  h3 {
    text-transform: uppercase;
    color: $white;
    padding-left: 1rem;
  }
  ul {
    list-style-type: none;
    margin-top: 50px;
    padding: 0;
    border-top: 2px solid tint($brand-lightBlue, 5%);
    li {
      border-bottom: 2px solid tint($brand-lightBlue, 5%);
      transition: .2s ease background-color;
      display: block;
      &:hover {
        background-color: $brand-orange;
      }
    }
  }
  a {
    padding: 0.625rem 1rem;
    color: $brand-yellow;
    text-decoration: none;
    display: block;
  }
}
